<style>
  .vip span{
    color: rgb(168, 168, 168);
  }
  .vip{
    width: 100%;
    height: 100%;
  }
  .vip>.container{
    margin: 3vw;
    padding: 0;
    display: flex;
    flex-direction: column;
  }
  .vip>.container>li{
    list-style: none;
    height: 60px;
    border-bottom: 1px solid #666;
    text-align: left;
    display: flex;
    align-items: center;
    padding-right: 3vw;
  }
  .vip>.container>li:first-child>img{
    height: 80%;
    margin-right: 3vw;
  }
  .vip>.container>li:not(:first-child){
    justify-content: space-between;
  }
  .vip>.container>li:not(:first-child)>span{
    transform: rotate(-90deg)
  }
</style>
<template>
  <div class="vip">
    <ul class="container">
      <li>
        <img src="../assets/images/user.jpg" alt="">
        <div>
          <h3>vue</h3>
          <span>绑定手机：</span>
        </div>
      </li>
      <li>
        <p>账号与安全</p><span>V</span>
      </li>
      <li @click="goCar">
        <p>我的购物车</p><span>V</span>
      </li>
      <li @click="goAddress">
        <p>收货地址管理</p><span>V</span>
      </li>
      <li @click="goAllOrder">
        <p>全部订单</p><span>V</span>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  data(){
    return {

    }
  },
  methods:{
    goAllOrder(){
      this.$router.push("/allorder")
    },
    goCar(){
      this.$router.replace("/car")
    },
    goAddress(){
      this.$router.push("/address")
    }
  },
  watch:{

  },
  components:{

  }
}
</script>